import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import jsPDF from 'jspdf';
import { initializeApp } from "firebase/app";
import { environment } from './../../environments/environment.prod';
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";



@Component({
  selector: 'app-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent implements OnInit {

  imagePath: string = "";
  firebaseApp = initializeApp(environment.firebaseConfig);
  storage = getStorage(this.firebaseApp, "gs://bintalhoda-ae42e.appspot.com");

  constructor(private httpClient: HttpClient) {

  }

  ngOnInit(): void {
  }


  uploadFile(event) {



    var image = event.target.files[0];
    const storageRef = ref(this.storage, 'pictures/photo.png');

    uploadBytes(storageRef, image).then((snapshot) => {
      console.log('Uploaded a blob or file!');
    });



    //display the image

    this.displayImage();



  }

  displayImage() {

    getDownloadURL(ref(this.storage, 'pictures/photo.png'))
      .then(url =>
        this.imagePath = url
      );



  }




  transform(event) {
    //create a pdf document
    let doc = new jsPDF({
      orientation: "portrait",
      unit: "cm",
      format: 'A4',
      compress: true
    });

    doc.addPage('A4', 'portrait');
    let img = new Image();

    //get the image
    var node = document.getElementById("container")
    domtoimage.toPng(node)
      .then((dataUrl) => {
        img.src = dataUrl;
        doc.addImage(img, 'JPEG', 0, 0, 21, 29.7, 'image', "FAST", 0);
        doc.save('aa' + ".pdf");
      })


  }
}




