import { Component, OnInit } from '@angular/core';
import { initializeApp } from "firebase/app";
import { environment } from './../../environments/environment.prod';
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { ViewChild, ElementRef } from '@angular/core';
import domtoimage from 'dom-to-image';
import jsPDF from 'jspdf';
import { faFilePdf } from '@fortawesome/free-solid-svg-icons';
import { faRedo } from '@fortawesome/free-solid-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { faMinus } from '@fortawesome/free-solid-svg-icons';
import { faPhotoVideo } from '@fortawesome/free-solid-svg-icons';






@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {

  faFilePdf = faFilePdf;
  faRedo = faRedo;
  faPlus = faPlus;
  faMinus = faMinus;
  faPhotoVideo = faPhotoVideo;
  imagePath: String = "";
  isLoading: boolean = false;
  firebaseApp = initializeApp(environment.firebaseConfig);
  storage = getStorage(this.firebaseApp, "gs://bintalhoda-ae42e.appspot.com");

  pdfSize = [4, 6];

  boldText = "";
  classicText = "";

  selectedSize = 4;


  @ViewChild('imgContainer') div: ElementRef;

  constructor() {

    this.showImage();
  }

  ngOnInit(): void {
  }

  onUpload(event) {
    this.isLoading = true;

    //value is set back to false at the end of show image method
    var image = event.target.files[0];
    const storageRef = ref(this.storage, 'pictures/photo.png');

    uploadBytes(storageRef, image).then((snapshot) => {
      console.log('Uploaded a blob or file!');
    });

    this.showImage();

  }


  showImage() {


    if (!this.isLoading) this.isLoading = true;
    getDownloadURL(ref(this.storage, 'pictures/photo.png'))
      .then(url => {

        this.imagePath = url
        console.log(this.imagePath)
        this.isLoading = false;
      }
      );


  }

  plusCadre(event) {
    let width = document.getElementById("image-container").offsetWidth
    width += 25;
    document.getElementById("image-container").style.width = width + "px";

  }

  minusCadre(event) {
    let width = document.getElementById("image-container").offsetWidth
    width -= 25;
    document.getElementById("image-container").style.width = width + "px";
  }

  plusImage(event) {
    let width = document.getElementById("image").offsetWidth
    width += 25;
    document.getElementById("image").style.width = width + "px";
    document.getElementById("text").style.width = width + "px";


  }


  minusImage(event) {
    let width = document.getElementById("image").offsetWidth
    width -= 25;
    document.getElementById("image").style.width = width + "px";
    document.getElementById("text").style.width = width + "px";


  }


  changePdfSize(event) {
    this.selectedSize = event.target.value;

  }


  createPdf(event) {

    let doc = new jsPDF({
      orientation: "portrait",
      unit: "cm",
      format: 'A4',
      compress: true
    });

    let size = this.selectedSize
    var node = document.getElementById("image-container");
    domtoimage.toPng(node)
      .then(function (dataUrl) {
        var img = new Image();
        img.src = dataUrl;

        if (size == 4) {
          var x = 0.3;
          var y = 0;
          doc.addImage(img, 'JPEG', x, y, 10, 14, 'image', "FAST", 0);

          x = 10.7;
          doc.addImage(img, 'JPEG', x, y, 10, 14, 'image', "FAST", 0);

          y = 14.7;
          x = 0.3;
          doc.addImage(img, 'JPEG', x, y, 10, 14, 'image', "FAST", 0);

          x = 10.7;
          doc.addImage(img, 'JPEG', x, y, 10, 14, 'image', "FAST", 0);
        }

        if (size == 6) {
          var x = 0.3;
          var y = 0;
          doc.addImage(img, 'JPEG', x, y, 10, 8, 'image', "FAST", 0);

          x = 10.7;
          doc.addImage(img, 'JPEG', x, y, 10, 8, 'image', "FAST", 0);

          y=9;
          x=0.3;
          doc.addImage(img, 'JPEG', x, y, 10, 8, 'image', "FAST", 0)

          x=10.7;
          doc.addImage(img, 'JPEG', x, y, 10, 8, 'image', "FAST", 0)

          y=18;
          x=0.3;
          doc.addImage(img, 'JPEG', x, y, 10, 8, 'image', "FAST", 0)

          x=10.7
          doc.addImage(img, 'JPEG', x, y, 10, 8, 'image', "FAST", 0)


         
        }



        doc.save('document.pdf');



      })
      .catch(function (error) {
        console.error('oops, something went wrong!', error);
      });
  }

}
